.wiGPT {
  /* ff 3.6+ */
  background: -moz-radial-gradient(
    circle at 0% 0%,
    rgba(0, 188, 212, 1) 0%,
    rgba(4, 44, 84, 1) 10%,
    rgba(6, 49, 93, 1) 100%
  );

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(
    circle at 0% 0%,
    rgba(0, 188, 212, 1) 0%,
    rgba(4, 44, 84, 1) 10%,
    rgba(6, 49, 93, 1) 100%
  );

  /* opera 11.10+ */
  background: -o-radial-gradient(
    circle at 0% 0%,
    rgba(0, 188, 212, 1) 0%,
    rgba(4, 44, 84, 1) 10%,
    rgba(6, 49, 93, 1) 100%
  );

  /* ie 10+ */
  background: -ms-radial-gradient(
    circle at 0% 0%,
    rgba(0, 188, 212, 1) 0%,
    rgba(4, 44, 84, 1) 10%,
    rgba(6, 49, 93, 1) 100%
  );

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 0% 0%,
    rgba(0, 188, 212, 1) 0%,
    rgba(4, 44, 84, 1) 10%,
    rgba(6, 49, 93, 1) 100%
  );
}

.wiGPT {
  padding: 5rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.wiGPT > .feature {
  align-items: center;
  gap: 2rem;
}

.wiGPT > .feature > .feature__title {
  flex: 0.3 1 27rem;
}
.wiGPT > .feature > .feature__description {
  flex: 0.7 1 60rem;
}
.wiGPT__export {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 9rem 0;
  gap: 1rem;
}
.wiGPT__export__title {
  font-size: 3.5rem;
  line-height: 4.5rem;
  font-weight: 800;
  max-width: 50rem;
}
.wiGPT__export__library {
  font-size: 1.6rem;
  line-height: 3rem;
  font-weight: 500;
  color: #ff8a71;
  /* font-family: "Manrope"; */
  cursor: pointer;
}
.wiGPT__features {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 2rem;
  /* flex-wrap: wrap; */
}

.wiGPT__features .feature {
  flex-direction: column;
  flex: 1;
  /* gap: 2rem; */
}
@media screen and (max-width: 1050px) {
  .wiGPT__features {
    flex-wrap: wrap;
    gap: 2rem;
    align-items: flex-start;
  }
  .wiGPT__features .feature {
    flex: 1 1 25rem;
  }
}

@media screen and (max-width: 750px) {
  .wiGPT__features {
    gap: 4rem;
  }
  .wiGPT__features .feature {
    flex: 1 1 50rem;
  }
}

