.article__container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.article__image {
  width: 100%;
  height: auto;
  overflow: hidden;
  max-height: 28rem;
}

.article__image img {
  width: 100%;
  height: 100%;
  /* max-width: 42rem; */
  object-fit: cotain;
}
.article__detail {
  flex: 1;
  padding: 3rem 2rem;
  background-color: #042c54;
  color: #ffffff;
  margin-top: -0.5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.article__date {
  font-weight: 700;
  font-size: 1.2rem;
  line-height: 3.5em;
}
.article__title {
  font-weight: 800;
  font-size: 2rem;
  line-height: 3rem;
}
.article__link {
  font-weight: 300;
  font-size: 1.2rem;
  line-height: 3.5rem;
}

@media screen and (max-width: 1250px) {
  .article__detail {
    padding: 2rem 1rem;
  }
}

@media screen and (max-width: 950px) {
  .article__detail {
    padding: 1rem 1rem;
  }
}
