.possibility {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
}
.possibility__image {
  flex: 1 1 40rem;
}

.possibility__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.possibility__detail {
  flex: 1 1 40rem;
}
.possibility__text {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 3rem;
  cursor: pointer;
  /* identical to box height, or 188% */

  color: #71e5ff;
}
.possibility__title {
  font-weight: 800;
  font-size: 3.4rem;
  line-height: 4.5rem;
  margin: 1rem 0 2rem;
}
.possibility__content {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 3rem;
  margin-bottom: 3rem;
  width: 70%;
  /* or 188% */

  color: #81afdd;
}

/* .text--orange {
  color: #ff8a71;
} */
.possibility__text:last-child {
  color: #ff8a71;
}
