/* .features
	.features__topic
		.features__title
		.features__access
	.features__content */
.features {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 20rem;
}

.features__topic {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  flex: 0.4 1 40rem;
}

.features__title {
  font-size: 3.5rem;
  line-height: none;
  font-weight: 800;
}

.features__access {
  font-size: 1.6rem;
  line-height: 3rem;
  font-weight: 500;
  cursor: pointer;
  color: #ff8a71;
}

.features__content {
  flex: 0.7 1 70rem;
  display: flex;
  flex-direction: column;
  gap: 4rem;
  align-items: flex-start;
  justify-content: center;
}

.features__content .feature {
  gap: 15rem;
  align-items: center;
}
.features__content .feature > .feature__title {
  flex: 0.3 1 12rem;
  font-size: 1.8rem;
  line-height: 2.4rem;
  font-weight: 800;
  letter-spacing: -4%;
}

.features__content .feature > .feature__description {
  flex: 0.5 1 25rem;
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 2.4rem;
}

@media screen and (max-width: 1050px) {
  .features {
    flex-wrap: wrap;
    gap: 3rem;
  }

  .features__content > .feature {
    gap: 2rem;
  }
}
