.CTA {
  padding: 4.5rem;
  background: linear-gradient(89.97deg, #ae67fa 1.84%, #f49867 102.67%);
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10.7236px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.CTA__detail {
  flex: 1 1 70em;
}
.CTA__request {
  font-weight: 500;
  font-size: 1.2rem;
  line-height: 3rem;
  /* or 250% */

  color: #0e0e0e;
}
.CTA__text {
  font-weight: 800;
  font-size: 2.5rem;
  line-height: 4.5rem;
  /* or 188% */
}
.CTA__button {
  flex: 1 1 30rem;
  display: flex;
  justify-content: flex-end;
}

.CTA__button button {
  outline: none;
  border: none;
  border-radius: 3rem;
  background-color: #000000;
  color: white;
  padding: 0.7rem 1.5rem;
  text-align: center;
  cursor: pointer;
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 3rem;
  min-width: 18rem;
}

@media screen and (max-width: 950px) {
  .CTA {
    padding: 3rem;
  }
  .CTA__text {
    line-height: 2rem;
  }
  .CTA__button {
    justify-content: center;
    margin-top: 2rem;
  }
}
