/* .header
	.header__content
		h1.header__title
		p.header__desc
		.header__email
			+input.header__email__input
			+button.header__email__button
		.header__people
	.header__image
		+img */
.header {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header__content {
  /* flex: 0.4 1 50rem; */
  flex: 1;
}

.header__title {
  font-weight: 800;
  font-size: 6.2rem;
  line-height: 7.5rem;
  /* or 121% */

  letter-spacing: -0.04em;
  margin-bottom: 3rem;
}

.header__desc {
  font-weight: 400;
  font-size: 2rem;
  line-height: 2.7rem;
  padding-right: 12rem;

  color: #81afdd;
}

.header__email {
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
  margin-top: 3rem;
}

.header__email__input {
  background: #052d56;
  padding: 2.2rem 3.2rem;
  border-radius: 0.5rem 0 0 0.5rem;
  color: #3d6184;
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.7rem;
  outline: none;
  border: none;
}

.header__email__button {
  font-family: var(--font-family);

  outline: none;
  border: none;
  border-radius: 0 0.5rem 0.5rem 0;
  padding: 2rem 3rem;
  color: white;
  text-transform: capitalize;
  font-weight: 700;
  font-size: 2rem;
  line-height: 2.7rem;
  cursor: pointer;
  background-color: #ff4820;
}

.header__people {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 3rem;
  gap: 1.5rem;
}

.header__people img {
  height: 3.6rem;
  object-fit: contain;
}

.header__people__detail {
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 3.5rem;
  color: white;
}

.header__image {
  /* flex: 0.6 1 50rem; */
  flex: 1;
}

.header__image img {
  width: 100%;
  object-fit: contain;
}

@media screen and (max-width: 1050px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  .header__desc {
    padding: 0;
  }
  .header__email {
    justify-content: center;
  }
  .header__email__input {
    flex: 0.8;
  }
  .header__email__button {
    flex: 0.3;
  }

  .header__people {
    justify-content: center;
  }
}

@media screen and (max-width: 550px) {
  .header__people {
    flex-direction: column;
    gap: 0;
  }
}
