/* -gpt3__navbar
	+gpt3__navbar-links
		+gpt3__navbar-links_logo
			+img
		+gpt3__navbar-links__container
			+a.gpt3__navbar-links__item home
			+a.gpt3__navbar-links__item whatisGPT3
			+a.gpt3__navbar-links__item openAi
			+a.gpt3__navbar-links__item Case Studies
			+a.gpt3__navbar-links__item library
	+gpt3__navbar-login
		+a.btn
		+a.btn.active */
.gpt3__navbar {
  padding: 6rem 6rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.gpt3__navbar-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 6rem;
}

.gpt3__navbar-links_logo {
  width: 6rem;
  height: 1.6rem;
}

.gpt3__navbar-links_logo img {
  width: 6rem;
  height: 1.6rem;
  /* object-fit: cover; */
}

.gpt3__navbar-links__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 3rem;
}

.gpt3__navbar-links__item {
  color: white;
  font-size: 1.8rem;
  line-height: 2.5rem;
  font-weight: 500;
  text-transform: capitalize;
}

.gpt3__navbar-menu {
  display: none;
  position: relative;
}

.gpt3__navbar-menu svg {
  cursor: pointer;
}

.gpt3__navbar-menu__container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 1rem;
  position: absolute;
  top: calc(50% + 3rem);
  right: 0rem;
  text-align: right;
  width: 30rem;
  padding: 2rem;
  border-radius: 0.5rem;
  background-color: var(--color-footer);
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu__container .gpt3__navbar-links__item:hover {
  text-decoration: underline;
}

.gpt3__navbar-menu__login {
  display: none;
}

@media screen and (max-width: 1000px) {
  .gpt3__navbar-links__container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
  .gpt3__navbar-login {
    margin-left: auto;
    margin-right: 4rem;
  }
}
@media screen and (max-width: 750px) {
  .gpt3__navbar {
    padding: 4rem;
  }
}
@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 4rem 2rem;
  }
  .gpt3__navbar-login {
    display: none;
  }
  .gpt3__navbar-menu__login {
    display: flex;
    flex-direction: column-reverse;
    gap: 1rem;
    align-items: flex-end;
    text-align: end;
  }
}
