.blog {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 3rem;
}

.blog__section__title {
  font-weight: 800;
  font-size: 6rem;
  line-height: 7.5rem;
  /* padding-right: 60%; */
  margin-bottom: 10rem;
  margin-top: 5rem;
}
.blog__item {
}

.blog__item--1 {
  grid-column: 1/2;
  grid-row: 1/3;
  display: flex;
  align-items: stretch;
}

.blog__item .article__image {
  max-height: 24rem;
}
.blog__item--1 > .article__container {
  height: 100%;
}

.blog__item--1 .article__image {
  max-height: 48rem;
}

.blog__item--1 .article__title {
  font-size: 2.5rem;
}

@media screen and (max-width: 950px) {
  .blog {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 750px) {
  .blog__item .article__title {
    font-size: 1.8rem;
  }

  .blog__item--1 .article__title {
    font-size: 2.2rem;
  }
}
@media screen and (max-width: 450px) {
  .blog {
    display: block;
  }

  .blog__item .article__image {
    max-height: 20rem;
  }

  .blog__item .article__title {
    font-size: 2rem;
  }
  .blog__item .article__container {
    margin-bottom: 2rem;
  }
}
