.feature {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  /* flex-wrap: wrap; */
}
.feature__title {
  font-size: 2.5rem;
  font-weight: 800;
  line-height: none;
  letter-spacing: -4%;
  color: white;
  position: relative;
}

.feature__title::after {
  content: "";
  position: absolute;
  top: -1rem;
  left: 0;
  width: 4rem;
  height: 0.3rem;
  background: linear-gradient(103.22deg, #ae67fa -13.86%, #f49867 99.55%),
    #ffffff;
}
.feature__description {
  font-weight: 500;
  font-size: 1.6rem;
  line-height: 3rem;
  color: #81afdd;
}

@media screen and (max-width: 1050px) {
  .feature__title {
    /* flex */
  }
}

@media screen and (max-width: 750px) {
  .feature {
    flex-wrap: wrap;
    gap: 2rem;
  }
}

