.footer {
  background-color: #031b34;
  padding-top: 10rem;
}
.footer__title {
  font-weight: 800;
  font-size: 6rem;
  line-height: 7.5rem;
  padding: 0 45rem;
  /* or 121% */

  text-align: center;
  letter-spacing: -0.04em;
  margin-bottom: 4rem;
}
.footer__button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__button .btn {
  border: 1px solid white;
  border-radius: none;
  padding: 2.2rem 2.4rem;
  font-size: 18px;
  line-height: 21px;
}
.footer__contact {
  margin-top: 15rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
}
.footer__logo {
}

.footer__logo_title {
  width: 11rem;
  height: 3rem;
  object-fit: contain;
}
.footer__logo_desc {
  font-size: 1.2rem;
  line-height: 1.5rem;
  color: #ffffff;
  margin-top: 1.5rem;
}
.footer__contact_list {
}
.footer__item {
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 2.6rem;
}
.footer__item-title {
  font-size: 1.4rem;
  line-height: 1.6rem;
  margin-bottom: 1rem;
  /* identical to box height */

  color: #ffffff;
}
.footer__item-link {
  font-size: 1.2rem;
  line-height: 2rem;
  /* or 167% */

  color: #ffffff;
}

@media screen and (max-width: 1550px) {
  .footer__title {
    padding: 0 15rem;
  }
}

@media screen and (max-width: 1250px) {
  .footer__title {
    padding: 0 10rem;
  }
}
@media screen and (max-width: 950px) {
  .footer__title {
    padding: 0 5rem;
  }
}
@media screen and (max-width: 750px) {
  .footer__title {
    padding: 0 3rem;
  }
}
@media screen and (max-width: 450px) {
  .footer__title {
    padding: 0 3rem;
  }
}
